import React, { useRef, useState, useCallback, useContext } from "react";
import { Box, Heading, ThemeContext } from "grommet";
import styled from "styled-components";
import { Interval } from "../../components/interval";
import { CryptomoodSvg } from "../../components/cryptomood-svg";
import { useCryptoSentimentQuery } from "../../generated/apollo";
import { currencyItem } from "./utils/utils";

const StyledIcon = styled.a`
  position: fixed;
  top: 42px;
  right: 0;
  transform: translate3d(119px, 0, 0);
  transition: ease 0.7s;
  :hover {
    right: 0;
    transform: translate3d(7px, 0, 0);
    transition: ease 0.7s;
  }
`;

export const CryptoSentimentComponent = () => {
  const params = useRef(new URLSearchParams(window.location.search));

  const theme: any = useContext(ThemeContext);
  const assets =
    params.current.get("assets") ||
    "BTC,ETH,XRP,NEO,BNB,BCH,LTC,GET,ONT,XLM,XMR,ADA,TRX,XTZ,MKR,ATOM,CRO";

  const currencies = assets.toUpperCase().split(",");

  const [currentInterval, setCurrentInterval] = useState("D1");
  const changeInterval = useCallback((interval: string) => {
    setCurrentInterval(interval);
  }, []);
  const inputIntervalButtons = [
    "1 Day",
    "1 Week",
    "2 Weeks",
    "1 Month",
    "3 Months"
  ];

  const { data, loading } = useCryptoSentimentQuery({
    variables: {
      assets: currencies,
      time_window: currentInterval
    }
  });

  return (
    //data &&
    <Box background={theme.background} pad="20px">
      <StyledIcon target="_" href={"https://cryptomood.com"}>
        <CryptomoodSvg width={155} height={40} />
      </StyledIcon>
      <Heading size="32px" textAlign="center" color={theme.primary}>
        Cryptocurrency sentiment
      </Heading>

      <Box pad="small">
        <Interval
          currentInterval={currentInterval}
          inputIntervals={inputIntervalButtons}
          changeInterval={changeInterval}
        />
      </Box>
      {loading ? (
        "Loading..."
      ) : (
        <Box direction="column">
          {data &&
            data.getAssetDetails.map((curr: any, key: number) =>
              currencyItem(curr, key, theme)
            )}
        </Box>
      )}
    </Box>
  );
};

export default CryptoSentimentComponent;
