import React from "react";
import { Box, Text, Meter, Image } from "grommet";
import { LinkUp, LinkDown } from "grommet-icons";
import Normal from "../icons/normal.svg";
import Euphoria from "../icons/euphoria.svg";
import Depression from "../icons/depression.svg";
import CM from "../icons/cryptomood.svg";

export const currencyItem = (curr: any, key: number, color: any) => {
  const sentiment =
    curr.social_sentiment_value < -3.33
      ? "Negative"
      : curr.social_sentiment_value > 3.33
      ? "Positive"
      : "Neutral";
  const sentimentColor =
    sentiment === "Negative"
      ? "red"
      : sentiment === "Positive"
      ? "green"
      : "orange";

  const sentimentIcon =
    sentiment === "Positive"
      ? Euphoria
      : sentiment === "Negative"
      ? Depression
      : Normal;

  const arrowLink =
    sentiment === "Positive" ? (
      <LinkUp color="green" size="19px" />
    ) : (
      sentiment === "Negative" && <LinkDown color="red" size="19px" />
    );

  return (
    <Box key={key} pad="small" direction="row" align="center" justify="between">
      <Box direction="row" align="center">
        <Box pad="small">
          <Image
            src={curr.icon}
            fallback={CM}
            alt={curr.id}
            width="48px"
            height="48px"
          />
        </Box>
        <Box pad="small">
          <Text size="22px" weight="bold" color={color.primary}>
            {curr.name}
          </Text>
        </Box>
        <Box pad="small">{arrowLink}</Box>
      </Box>
      <Box direction="row" align="center">
        <Image src={sentimentIcon} width={20} height={20} margin="xsmall" />
        <Box pad="small">
          <Text color={sentimentColor} size="16px">
            {sentiment}
          </Text>
        </Box>
        <Box style={{ transform: "rotate(180deg)" }} gap={"none"}>
          <Meter
            round
            size="30px"
            thickness="xsmall"
            values={[
              {
                value:
                  curr.social_sentiment_value < 0
                    ? Math.abs(curr.social_sentiment_value) * 10
                    : 0,
                label: "sentiment",
                color: sentimentColor
              }
            ]}
            background={{ color: "#EEEEEE" }}
          />
        </Box>
        <Meter
          round
          size="30px"
          thickness="xsmall"
          values={[
            {
              value:
                curr.social_sentiment_value > 0
                  ? curr.social_sentiment_value * 10
                  : 0,
              label: "sentiment",
              color: sentimentColor
            }
          ]}
          background={{ color: "#EEEEEE" }}
        />
      </Box>
    </Box>
  );
};
